h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

p {
	margin: 0;
}

ul,
ol {
	margin: 0;
	padding: 0;
	list-style: none;
}

.link {
	text-decoration: none;
}

.list {
	list-style: none;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

a {
	display: block;
	max-width: 100%;
	text-decoration: none;
}

body {
	font-family: $main-font;
	font-style: normal;
	// background-image: ;
	;
}

%section {
	padding-top: 40px;
	padding-bottom: 40px;

	@media screen and (min-width: $desktop) {
		padding-top: 80px;
		padding-bottom: 80px;
	}
}

%container {
	@media screen and (min-width: 320px) {
			width: 320px;
			padding-right: 20px;
			padding-left: 20px;
		}

	@media screen and (min-width: $tablet) {
		width: 768px;
		padding-right: 32px;
		padding-left: 32px;
	}
	
	@media screen and (min-width: $desktop) {
		width: 1680px;
		padding-right: 16px;
		padding-left: 16px;
	}
	margin-left: auto;
	margin-right: auto;
}

%container__title {
	@include font(800, 20px, 1.3, 0.03em);

	@media screen and (min-width: $tablet) {
		font-size: 26px;
	}

	@media screen and (min-width: $desktop) {
		font-size: 46px;
	}
}

%visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}

